import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    type: '',
    mobile: '',
    token: '',
    isLogin: false,         // 
    statusBarHeight: 20,    // 
    windowHeight: '',       // ,
    fontSize: 16,           // 
    // footData: {
    //   encryptedData: '',
    //   iv: ''
    // },           // 
  },
  mutations: {
    login(state, userinfo) {
      state.mobile = userinfo.mobile;
      state.token = userinfo.token;
    },
    loginout(state) {
      state.mobile = '';
      state.token = '';
    },
    setToken(state, token) {
      state.token = token
    },
    setHeight(state, res) {
      state.statusBarHeight = res.statusBarHeight
      state.windowHeight = res.windowHeight
      state.fontSize = res.fontSizeSetting
    },
    redirectLoginPage(state, res) {
      state.isLogin= res.status
    },
    // setFootData(state, res) {
    //   state.footData= res
    // }
  }
})

export default store
